/*
 * Global base style
 */

/* Dependencies */
import { createGlobalStyle } from 'styled-components';
import zpixWOFF from '@fonts/Zpix.woff';
import zpixTTF from '@fonts/Zpix.ttf';

/* base styles */
const GlobalStyle = createGlobalStyle`
  @font-face {
    font-family: 'Zpix';
    src: url('${zpixWOFF}') format('woff'), url('${zpixTTF}') format('truetype');
    font-weight: normal;
    font-style: normal;
  }

  * {
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
  }

  html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Zpix',"PingFang TC","Lantinghei TC","Heiti TC","Apple LiGothic",微軟正黑體,"Microsoft JhengHei","Microsoft JhengHei UI","Microsoft YaHei","Microsoft YaHei UI","PMingLiU",Arial,sans-serif;
    font-size: 18px;
    line-height: 1.5em;
    text-align: left;
    background-color: ${props => props.theme.backgroundColor};
    color: ${props => props.theme.mainTextColor};
    scroll-behavior: smooth;
    cursor: url('${props => props.theme.cursorDefault}') 0 0, pointer;
  }

  ::selection {
    background: ${props => props.theme.highlightColor}; /* WebKit/Blink Browsers */
  }

  ::-moz-selection {
    background: ${props => props.theme.highlightColor}; /* Gecko Browsers */
  }

  h1, h2, h3, h4, h5, h6 {
    font-weight: bold;
  }

  img {
    border: 0;
  }

  p {
    margin: 1em 0;
  }

  a {
    color: ${props => props.theme.linkColor};
    text-decoration: none;
    cursor: url('${props => props.theme.cursorPointer}') 5 0, pointer;

    &:hover {
      color: ${props => props.theme.linkHoverColor};
    }
  }

  #root, #app {
    height: 100%;
  }
`;

export default GlobalStyle;
